:host { }

.fle-info {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: flex-start;
  overflow: hidden;
  padding: 3rem;
  // use color variables
  background-color: #003e6270;
  color: white;
  font-weight: 100;
  gap: 16px;

  h2 {
    color: $primary-color;
    font-size: $title-text-size;
    font-weight: 100;
  }

  p {
    font-size: $normal-text-size;
    font-weight: 100;
  }
}
